import { GLTF, GLTFLoader, GLTFParser } from "three/examples/jsm/loaders/GLTFLoader"
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js';
import { mergeBufferAttributes, mergeBufferGeometries } from 'three/examples/jsm/utils/BufferGeometryUtils.js';
import * as THREE from "three";
import { ThreeScene } from "@/sence/ThreeScene";
import { Box3, BufferAttribute, BufferGeometry, Color, InstancedMesh, Material, Matrix4, Mesh, MeshBasicMaterial, MeshStandardMaterial, ShaderLib, ShaderMaterial, ShaderMaterialParameters, UniformsUtils } from "three";
import { ref } from "vue";
import { ElementData } from "@/data/ElementData";
import { InstanceData } from "@/data/InstanceData";
import { BatchData } from "@/data/BatchData";
import { MaterialData } from "@/data/MaterialData";
import { ModelData } from "@/data/ModelData";
import { ProjectViewComponent } from "@/components/ProjectViewComponent";
import { View3DComponent } from "@/components/View3DComponent";


export class GlbLoaders {



    public static gltfLoader(world: ThreeScene, gltfUrl: string,name:string) {
        if (world.mapModels.has(gltfUrl)) return;

        world.clearModel();

        let scope = world;
        let loader = new GLTFLoader();
        let dracoLoader = new DRACOLoader();
        //GLTFParser.prototype["oldGetMaterialType"]=GLTFParser.prototype.getMaterialType;

        // GLTFParser.prototype.getMaterialType=function()
        // {
        //     return MeshStandardMaterial
        // }


        dracoLoader.setDecoderPath('./lib/draco/');
        loader.setDRACOLoader(dracoLoader);
        View3DComponent.showLoading.value=true;
        loader.load(gltfUrl, function (glTF) {
            //scope.obj.add(glTF.scene);
            glTF.scene.translateY(Math.random()*200)

            let box=new THREE.Box3().setFromObject(glTF.scene)
            scope.setCameraByBox(box);

            let modelData=new ModelData()
            scope.mapModels.set(gltfUrl,modelData)
            modelData.name=name
            modelData.boundingBox=box;
            modelData.rootObject=new THREE.Object3D();
            scope.obj.add( modelData.rootObject)
            GlbLoaders.batchAndInstance(glTF, modelData)
            //scope.calSenceBox();
            View3DComponent.showLoading.value=false;
            dracoLoader.dispose();

            modelData.showSpaceTree()


            URL.revokeObjectURL(gltfUrl);
        }, undefined, function (e) {
            window.alert(e);
            dracoLoader.dispose();
        });
    
    }


    

    private static batchAndInstance(glTF: GLTF,  modelData: ModelData) {

       
        //
        let model = glTF.scene;

        let structMap = new Map();
        //


        let idMap :Map<number,number>= new Map();

        model.traverse(function (child) {
            if (child instanceof THREE.Mesh&& child.geometry.getAttribute("position")!=null) {
                

                let curNode = child;
                let result = false;
                let getRoot = (object) => {
                    if (result) return
                    if (object && object.parent) {
                        if (object.parent.name == "root") {
                            curNode = object;
                            result = true;
                            return;
                        }
                        else {
                            getRoot(object.parent)
                        }
                    }
                }
                getRoot(curNode)
                let bufferGeometry = (child.geometry as BufferGeometry);
                let positionCount = child.geometry.getAttribute("position").count;
                let colors = []
                let id=curNode.id
                if (!idMap.has(curNode.id)) {
                    let elementData = new ElementData();
                    const box3 = new Box3();
                    box3.expandByObject(curNode);
                    elementData.boundingBox=box3
                    elementData.name = curNode.userData.name
                    elementData.useData = curNode.userData;
                    modelData.allElementData.set(elementData.id, elementData);
                    idMap.set(curNode.id,elementData.id)
                    id=elementData.id 
                }
                else
                {
                    id= idMap.get(curNode.id);
                }
                // if (child.material.map == null&&child.material.opacity==0) {     
                //     for (let index = 0; index < positionCount; index++) {
                //         colors.push( child.material.color.r);
                //         colors.push( child.material.color.g);
                //         colors.push( child.material.color.b);
                //     }
                //     child.material.name="batchColorMaterial"
                // }
                // else {
                //     for (let index = 0; index < positionCount; index++) {
                //         colors.push(1);
                //         colors.push(1);
                //         colors.push(1);
                //     }   
                // }
                for (let index = 0; index < positionCount; index++) {
                    colors.push( 1);
                    colors.push( 1);
                    colors.push( 1);
                }
                bufferGeometry.setAttribute('color', new THREE.Float32BufferAttribute(colors, 3));
                bufferGeometry['elementID']= id
                child['elementID'] = id;
                if (structMap.has(child.material.name)) {
                    let sameMesh = structMap.get(child.material.name);
                    sameMesh.push(child);
                    structMap.set(child.material.name, sameMesh);
                }
                else {
                    let sameMesh = [];
                    sameMesh.push(child);
                    structMap.set(child.material.name, sameMesh);
                }
            }
        });


        //structMapvalues
        for (let item of structMap.values()) {

            let instanceMap: Map<string, THREE.Mesh[]> = new Map();
            for (const element of item) {
                if (instanceMap.has(element.geometry.uuid)) {
                    let sameMesh = instanceMap.get(element.geometry.uuid);
                    sameMesh.push(element);
                    instanceMap.set(element.geometry.uuid, sameMesh);
                }
                else {
                    let sameMesh = [];
                    sameMesh.push(element);
                    instanceMap.set(element.geometry.uuid, sameMesh);
                }
            }

            let geometries :BufferGeometry[]= []; // geometry
            let material :Material
            for (const meshs of instanceMap.values()) {
                let meshCount = meshs.length;
                if (meshCount > 1) {
                    let instancedMeshGeometry = meshs[0].geometry;
                    let instancedMesh = new THREE.InstancedMesh(instancedMeshGeometry, meshs[0].material, meshCount);
                    instancedMesh["orignMaterial"]=meshs[0].material;
                    instancedMesh["pickMaterial"]=MaterialData.pickMaterial
                    //let pickColors = new THREE.InstancedBufferAttribute(new Float32Array(meshCount*3 ), 3);
                    let elementIDS = new THREE.InstancedBufferAttribute(new Int32Array(meshCount ), 1);
                    //const pickColor = new THREE.Color();      
                    for (let i = 0; i < meshs.length; i++) {
                        const mesh = meshs[i];
                        let m = mesh.matrix;
                        let getMatrix4 = (obj) => {
                            if (obj.parent) {
                                m = m.premultiply(obj.parent.matrix);
                                getMatrix4(obj.parent)
                            }
                        }
                        let elementID = mesh['elementID']
                        getMatrix4(mesh);              
                        instancedMesh.setMatrixAt(i, m);
                        instancedMesh.setColorAt(i, MaterialData.normalColor);    
                        let instanceData = new InstanceData(instancedMesh,i);
                        modelData.allElementData.get(elementID).addMeshData(instanceData)
                        // pickColor.setHex( elementID);
                        // pickColors.setXYZ( i, pickColor.r, pickColor.g, pickColor.b );
                        elementIDS.setX(i,elementID)
                    }
                    //instancedMeshGeometry.setAttribute('pickColor', pickColors);
                    instancedMeshGeometry.setAttribute('elementID', elementIDS);
                    modelData.rootObject.add(instancedMesh);
                }
                else  {
                    for (let i = 0; i < meshs.length; i++) {
                        let ele: Mesh = meshs[0];
                        let m = (ele as THREE.Mesh).matrix;
                        let getMatrix4 = (obj) => {
                            if (obj.parent) {
                                m = m.premultiply(obj.parent.matrix);
                                getMatrix4(obj.parent)
                            }
                        }
                        getMatrix4(ele);
                        let geom = ele.geometry.applyMatrix4(m);
                        geometries.push(geom);
                        material = ele.material as Material;
                    }              
                }
            }
            // 
            if (geometries.length > 0) {
                material.vertexColors = true; //
                const mergedGeometry = new BufferGeometry();
                const singleMergeMesh = new THREE.Mesh(mergedGeometry, material);
                singleMergeMesh["orignMaterial"]=material;
                singleMergeMesh["pickMaterial"]=MaterialData.pickMaterial
                modelData.rootObject.add(singleMergeMesh); // mesh()         
                const isIndexed = geometries[ 0 ].index !== null;  
                const attributesUsed = new Set( Object.keys( geometries[ 0 ].attributes ) );       
                const attributes = {};
                let indexOffset = 0;
                const mergedIndex = []; 
                //let pickColors = []
                let elementIDS = []
                //const pickColor = new THREE.Color();
                for ( let i = 0; i < geometries.length; ++ i ) {
                    const geometry = geometries[ i ];
                    let elementID =  geometry['elementID'];
                    let batchData=new BatchData(singleMergeMesh,elementID);
                    modelData.allElementData.get(elementID).addMeshData(batchData)
                    if ( isIndexed !== ( geometry.index !== null ) ) {
                        continue;
                    }
                    const index = geometries[ i ].index;
                    for ( let j = 0; j < index.count; ++ j ) {
                        let curIndex=index.getX( j ) + indexOffset 
                        mergedIndex.push( curIndex);
                        batchData.indexs.push(curIndex)
                    }
                    //pickColor.setHex(elementID);
                    let positionCount = geometry.attributes.position.count
                    for (let index = 0; index < positionCount; index++) {
                        // pickColors.push(pickColor.r)
                        // pickColors.push(pickColor.g)
                        // pickColors.push(pickColor.b)
                        elementIDS.push(elementID)
                    } 
                    indexOffset += geometries[ i ].attributes.position.count;
                    for ( const name in geometry.attributes ) {
            
                        if ( ! attributesUsed.has( name ) ) {
                            continue;
                        }
                        if ( attributes[ name ] === undefined ) attributes[ name ] = [];
                        attributes[ name ].push( geometry.attributes[ name ] );      
                    }
                    geometry.dispose();
                }
                if ( isIndexed ) {
                    mergedGeometry.setIndex( mergedIndex );
                }
                for ( const name in attributes ) {
                    const mergedAttribute = mergeBufferAttributes( attributes[ name ] );
                    if ( ! mergedAttribute ) {
                        continue;
                    }
                    mergedGeometry.setAttribute( name, mergedAttribute );
                }
                //mergedGeometry.setAttribute('pickColor', new THREE.Float32BufferAttribute(pickColors, 3));
                mergedGeometry.setAttribute('elementID', new THREE.Int32BufferAttribute(elementIDS, 1));
            } 
        }
    }



}