
import * as THREE from "three";
import { GlbLoaders } from "./loaders/GlbLoaders";
import { TilesLoaders } from "./loaders/TilesLoaders";
import { OperationBase } from "./operation/OperationBase";
import { OperationType } from "./operation/OperationType";
import { ThreeScene } from "./sence/ThreeScene";





export class App {


  public static scene: ThreeScene;
  public static currentOperation: OperationBase;
  public static allOperation: Map<string, OperationBase>;
  public static Base: OperationBase;



  constructor(goldenContainer) {
    App.scene = new ThreeScene(goldenContainer);
    App.Base = new OperationBase();

    App.allOperation = new Map();
    App.allOperation.set(OperationType.Base, App.Base)
    App.currentOperation = App.Base;
    App.currentOperation.Enter();

   




   
    for (let type in OperationType) {
      let element = document.getElementById(type);
      if (element) {
          element.onclick = function () {App.runCommand(type); }
      }
    }




    document.getElementById("fileInputGLTF").addEventListener('input', async (event) => {
      let srcElement: any = event.srcElement;
      let glbfUrl = URL.createObjectURL(srcElement.files[0]);
      GlbLoaders.gltfLoader(App.scene,glbfUrl,srcElement.files[0].name);
    });
    //GlbLoaders.gltfLoader(App.scene,"2.glb","2");
    //GlbLoaders.gltfLoader(App.scene,"test.glb");
    //TilesLoaders.tilesLoaders(App.scene,"JZ/tileset.json"); 
    //TilesLoaders.tilesLoaders(App.scene,"3dtiles/tileset.json");
    // TilesLoaders.tilesLoaders(App.scene,"3dtiles/jz/tileset.json");
    // TilesLoaders.tilesLoaders(App.scene,"3dtiles/jd/tileset.json");

    let param= new URLSearchParams(window.location.search || window.location.hash.substr(1))
    if(param.has("model"))
    {
      let id=param.get("model")
      switch (id) {
        case "1":
          GlbLoaders.gltfLoader(App.scene,"glb/1.glb","rac_basic_sample_project");
          break;
        case "2":
          GlbLoaders.gltfLoader(App.scene,"glb/2.glb","rac_advanced_sample_project");
          break;
        case "3":
          TilesLoaders.tilesLoaders(App.scene,"3dtiles/tileset.json");
          break;
        default:
          break;
      }
    }
  }

  public static runCommand(data: string) {
    App.currentOperation.Quit();
    for (let type of this.allOperation.keys()) {
      if (type == data) {
        App.currentOperation = this.allOperation.get(type);
        App.currentOperation.Enter();
        return;
      }
    }
  }
}