import { Mesh, BufferAttribute, Color, Box3, Vector3 } from "three";
import { MeshData } from "./MeshData";
/**
 * 
 */
export class BatchData implements MeshData
{

    constructor(mesh: Mesh,batchID: number)
    {
        this.mesh=mesh;
        this.batchID=batchID;
        this.indexs=[]
    }
    public mesh: Mesh;
    public batchID: number;
    public orignPositions:Array<number[]>;
    //public orignColors:Array<number[]>;
    public indexs:Array<number>;

    private setOrignPositions()
    {
        let positions: BufferAttribute = this.mesh.geometry.attributes.position as BufferAttribute;
        if(this.orignPositions==null)
        {
            this.orignPositions=[]
            for (let i = 0; i < this.indexs.length; i++) {
                const index = this.indexs[i];
                this.orignPositions.push([positions.getX(index),
                    positions.getY(index),
                    positions.getZ(index)])
            }
        }
    }



    private _show:boolean=true
    public set show(value:boolean)
    {
        if(this._show==value) return;
        this._show=value;
        let positions: BufferAttribute = this.mesh.geometry.attributes.position as BufferAttribute;
        this.setOrignPositions();
        if(value)
        {
            for (let i = 0; i < this.indexs.length; i++) {
                const index = this.indexs[i];
                positions.setX(index, this.orignPositions[i][0]);
                positions.setY(index, this.orignPositions[i][1]);
                positions.setZ(index, this.orignPositions[i][2]);
            }
        }
        else
        {
            for (let i = 0; i < this.indexs.length; i++) {
                const index = this.indexs[i];
                positions.setX(index, 0);
                positions.setY(index, 0);
                positions.setZ(index, 0);
            }
        }
        positions.needsUpdate = true;
    }
    public get show():boolean
    {
        return this._show;
    }

    

    private _color:Color=null;
    public set color(value:Color)
    {
        // if(this.orignColors==null)
        // {
        //     let colors: BufferAttribute = this.mesh.geometry.attributes.color as BufferAttribute;
        //     this.orignColors=[]
        //     for (let i = 0; i < this.indexs.length; i++) {
        //         const index = this.indexs[i];
        //         this.orignColors.push([colors.getX(index),
        //             colors.getY(index),
        //             colors.getZ(index)])
        //     }
        // }
        if(value==null)
        {
            let colors: BufferAttribute = this.mesh.geometry.attributes.color as BufferAttribute;
            for (let i = 0; i < this.indexs.length; i++) {
                const index = this.indexs[i];
                //colors.setXYZ(index, this.orignColors[i][0], this.orignColors[i][1], this.orignColors[i][2]);
                colors.setXYZ(index,1,1,1)
            }
            colors.needsUpdate = true;
            return;
        }
        this._color=value;
        let colors: BufferAttribute = this.mesh.geometry.attributes.color as BufferAttribute;
        for (let i = 0; i < this.indexs.length; i++) {
            const index = this.indexs[i];
            colors.setXYZ(index,value.r,value.g,value.b)
        }    
        colors.needsUpdate = true;
    }
    public get color():Color
    {
        return this._color;
    }


    public _boundingBox: Box3
    public set boundingBox(value: Box3) {
        this._boundingBox = value;
    }
    public get boundingBox(): Box3 {
        if (this._boundingBox == null) {
            this.setOrignPositions();

            // let minx=Number.MAX_VALUE
            // let miny=Number.MAX_VALUE
            // let minz=Number.MAX_VALUE
            // let maxx=Number.MIN_VALUE
            // let maxy=Number.MIN_VALUE
            // let maxz=Number.MIN_VALUE
            let minx=Number.MAX_VALUE
            let miny=Number.MAX_VALUE
            let minz=Number.MAX_VALUE
            let maxx=-Number.MAX_VALUE
            let maxy=-Number.MAX_VALUE
            let maxz=-Number.MAX_VALUE
            for (const p of this.orignPositions) {
                minx=Math.min(minx,p[0]);
                miny=Math.min(miny,p[1]);
                minz=Math.min(minz,p[2]);
                maxx=Math.max(maxx,p[0]);
                maxy=Math.max(maxy,p[1]);
                maxz=Math.max(maxz,p[2]);
            }
            let min=new Vector3(minx,miny,minz)
            let max=new Vector3(maxx,maxy,maxz)
            this._boundingBox =new Box3(min,max)
            let m = this.mesh.matrix.clone();
            let getMatrix4 = (obj) => {
                if (obj.parent) {
                    m = m.premultiply(obj.parent.matrix);
                    getMatrix4(obj.parent)
                }
            }
            getMatrix4(this.mesh);
            this._boundingBox.applyMatrix4(m)      
        }
        return this._boundingBox;
    }
}
