import { App } from "@/App";
import { MaterialData } from "@/data/MaterialData";
import { GlbLoaders } from "@/loaders/GlbLoaders";
import * as THREE from "three"
import { BufferAttribute, Color, InstancedMesh } from "three";
import { OperationType } from "./OperationType";

export class OperationBase {

  public ListenerMouseDown = null;
  public ListenerMouseMove = null;
  public ListenerMouseUp = null;
  public ListenerkeyDown = null;


  public highlightedObj = null;
  public highlightedIndex: number = 0;

  protected operationType: string;


  protected lastX: number = 0;
  protected lastY: number = 0;



  //id
  public selID: number = -1;

  private _moveId: number = null

  constructor() {
    this.operationType = OperationType.Base;

    this.ListenerMouseDown = this.onMouseDown.bind(this);
    this.ListenerMouseMove = this.onMouseMove.bind(this);
    this.ListenerMouseUp = this.onMouseUp.bind(this);
    this.ListenerkeyDown = this.onKeyDown.bind(this);
  }



  public Enter(): void {
    this.enter();
  }
  public Quit(): void {
    this.quit();
  }
  protected enter(): void {
    this.eventListener(true);
  }





  protected onMouseDown(event: MouseEvent) {
    this.lastX = event.clientX;
    this.lastY = event.clientY;
  }




  protected onMouseMove(event: MouseEvent) {
    // if(App.scene.firstPerson) return
    // let resut1 = this.selElemetColorByID(this._moveId, null)
    // if (this.lastX) {
    //   if (resut1) {
    //     App.scene.enableRender();
    //   }
    //   return;
    // }
    // this._moveId = App.scene.raycaster.getIntersectObjByGPU(event);
    // if (this._moveId == this.selID) {
    //   this._moveId = null;
    //   return;
    // }
    // let result2 = this.selElemetColorByID(this._moveId, MaterialData.selColor)
    // if (resut1 || result2) {
    //   App.scene.enableRender();
    // }
  }


  protected onMouseUp(event: MouseEvent) {
    let x = event.clientX - this.lastX;
    let y = event.clientY - this.lastY;
    if ((Math.abs(x) <= 1 && Math.abs(y) <= 0)) {
      // let id=-1;
      // let intersects = App.scene.raycaster.getIntersectObj(event);
      // if (intersects.length != 0) {       
      //   if (intersects[0].object instanceof InstancedMesh) {
      //     let instancedMesh = intersects[0].object as THREE.InstancedMesh;
      //     let instanceId = intersects[0].instanceId;
      //     id= instancedMesh["IdMap"].get(instanceId);
      //   }
      //   else {
      //     let curNode = intersects[0].object as THREE.Mesh;
      //     let batchIDs: BufferAttribute = curNode.geometry.getAttribute("_batchid") as BufferAttribute;
      //     let batchID = batchIDs.getX(intersects[0].face.a);
      //     id=batchID;
      //   }
      // }
      let id = App.scene.raycaster.getIntersectObjByGPU(event);
      this.selElemetByID(id);
    }
    App.scene.enableRender();

    this.lastX = null;
    this.lastY = null;
    this._moveId = null;
  }






  public selElemetColorByID(id: number, color: Color) {
    for (const iterator of App.scene.mapModels.values()) {
      if (iterator.allElementData.has(id)) {
        iterator.allElementData.get(id).color = color
        return true
      }
    }
    return false
  }

  public selElemetByID(id: number) {
    this.resetSelEntity();
    for (const iterator of App.scene.mapModels.values()) {
      if (iterator.allElementData.has(id)) {
        let element=iterator.allElementData.get(id);
        element.color = MaterialData.selColor
        this.selID = id
          if (element.boundingBox&& !App.scene.firstPerson) {
            App.scene.setOrbitPoint((element.boundingBox.min.x + element.boundingBox.max.x) / 2,
            (element.boundingBox.min.y + element.boundingBox.max.y) / 2,
            (element.boundingBox.min.z + element.boundingBox.max.z) / 2,)
            App.scene.showBox3(element.boundingBox)
          }
        break;
      }
    }
    this.showProperty(id)
  }


  private resetSelEntity() {
    // if(GlbLoaders.allElementData.has(this.selID))
    // {
    //     GlbLoaders.allElementData.get(this.selID).color=null
    // }
    for (const iterator of App.scene.mapModels.values()) {
      if (iterator.allElementData.has(this.selID)) {
        iterator.allElementData.get(this.selID).color = null
        break;
      }
    }
    this.selID = -1;
  }


  public showProperty(id: number) {
    let div = document.getElementById("create-page");
    div.innerHTML = "";
    if (id == null) return
    for (const iterator of App.scene.mapModels.values()) {
      if (iterator.allElementData.has(id)) {
        let element = iterator.allElementData.get(id)
        if(element.useData)
        {
          let datas = []

          for (let name in element.useData) {
            if (name != "Parameters") {
                let value = element.useData[name]
                let data = { name, value }
                data.name = name;
                data.value = value;
                datas.push(data)
            }
        }

          this.setProperty("", datas)
          if (element.useData.Parameters) {
            for (let group of element.useData.Parameters) {
              this.setProperty(group.GroupName, group.Parameters)
            }
          }
        }
        break;
      }
    }
  }

  private setProperty(name: string, useData: any) {
    let div = document.getElementById("create-page");
    let createPage = document.createElement("div");
    createPage.className = "page-bar"
    let createLable = document.createElement("div");
    createLable.innerHTML = name;
    createLable.className = "pageBar-top"
    createPage.appendChild(createLable);
    let table = document.createElement("table");
    table.className = "gridtable";
    if (useData && useData.length) {
      for (let i = 0; i < useData.length; i++) {
        let data = useData[i];
        let row = table.insertRow(i);
        let c1 = row.insertCell(0);
        c1.innerHTML = data.name;
        let c2 = row.insertCell(1);
        if (data.value != null) {
          c2.innerHTML = data.value;
        }
      }
    }
    createPage.appendChild(table);
    div.appendChild(createPage);
  }



  protected quit(): void {
    this.eventListener(false);
  }
  public eventListener(listen: boolean) {
    if (listen) {
      App.scene.renderer.domElement.addEventListener('pointerdown', this.ListenerMouseDown);
      App.scene.renderer.domElement.addEventListener('mousemove', this.ListenerMouseMove);
      App.scene.renderer.domElement.addEventListener('pointerup', this.ListenerMouseUp);
      document.addEventListener('keydown', this.ListenerkeyDown);
    }
    else {
      App.scene.renderer.domElement.removeEventListener('pointerdown', this.ListenerMouseDown);
      App.scene.renderer.domElement.removeEventListener('mousemove', this.ListenerMouseMove);
      App.scene.renderer.domElement.removeEventListener('pointerup', this.ListenerMouseUp);
      document.removeEventListener('keydown', this.ListenerkeyDown);
    }
  }

  protected onKeyDown(event) {
    if (event.code === 'Escape' || event.keyCode === 27) {
      this.changeOperation();
    }
  }
  public changeOperation() {
    if (App.currentOperation.operationType != OperationType.Base) {
      App.currentOperation.Quit();
      App.currentOperation = App.Base;
      App.currentOperation.Enter();
    }
  }

}