import { App } from "@/App";
import gsap from "gsap";
import * as THREE from "three";
import { CameraControls } from "./controls/CameraControls";
interface LookAt
{
    position: THREE.Vector3
    target: THREE.Vector3
    duration:number
    url:string
}

export class PathAnimation
{


    private static id=0

    public paths:Array<LookAt>


    //public duration: number=3;

    private _tmp: THREE.Vector3;

    private _cameraControls: CameraControls;

    public name: string;


    public selIndex: number;

    constructor(cameraControls:CameraControls)
    {
        this._tmp=new THREE.Vector3();
        this.paths=[];
        this._cameraControls=cameraControls;
        this.name=""+PathAnimation.id++
        this.selIndex=0
    }
 
    
    public play(index:number)
    {
        if(this.paths.length<=1||this.paths.length==index+1) return;

        let cameraPosition:THREE.Vector3[]=[]
        let cameraTarget:THREE.Vector3[]=[]
        for (let i =index; i < this.paths.length; i++) {
            const path = this.paths[i];
            cameraPosition.push(path.position)
            cameraTarget.push(path.target)
        }

        const cameraCurve = new THREE.CatmullRomCurve3(cameraPosition);
        const targetCurve  = new THREE.CatmullRomCurve3(cameraTarget);

        const animationProgress = { value: 0 };
        let start=0
        let time=1/(this.paths.length-1)
        let pathIndex=0
        this._paly(cameraCurve,targetCurve,animationProgress,start,time,this.paths[0].duration,pathIndex)
    }



    private _paly(cameraCurve:THREE.CatmullRomCurve3,targetCurve:THREE.CatmullRomCurve3,animationProgress:any,start:number,time:number,duration:number,pathIndex:number)
    {
        const tmp = this._tmp
        const cameraControls = this._cameraControls
        const source =this
        const pathAnimation = gsap.fromTo(
            animationProgress,
            {
                value: start,
            },
            {
                value: start+time,
                duration: duration,//
                overwrite: true,
                paused: true,
                ease:"none",
                onUpdateParams: [ animationProgress ],
                onUpdate( { value } ) {
                    if ( ! this.isActive() ) return;
                    cameraCurve.getPoint ( value, tmp);
                    const cameraX = tmp.x;
                    const cameraY = tmp.y;
                    const cameraZ = tmp.z;
                    targetCurve.getPoint ( value,tmp);
                    const lookAtX =tmp.x;
                    const lookAtY = tmp.y;
                    const lookAtZ = tmp.z;
        
                    cameraControls.setLookAt(
                        cameraX,
                        cameraY,
                        cameraZ,
                        lookAtX,
                        lookAtY,
                        lookAtZ,
                        false, // IMPORTANT! disable cameraControls's transition and leave it to gsap.
                    );
                    cameraControls.update()
                    App.scene.render();
                },
                onStart() {
        
                    cameraControls.enabled = false;
        
                },
                onComplete() {
                    start+=time
                    if(start>=1)
                    {
                        cameraControls.enabled = true;
                        return;
                    }
                    pathIndex++
                    source._paly(cameraCurve,targetCurve,animationProgress,start,time,source.paths[pathIndex].duration,pathIndex)
                },
            }
        );

        pathAnimation.play( 0)
    }

    
}