import { App } from "@/App";
import { Color, Material, UniformsUtils,Shader } from "three";


export class ShaderUtils
{
   
    public static batchIdHighlightShader(material:Material) {

        material.onBeforeCompile = function (shader:Shader) {

            shader.uniforms = {
                highlightedBatchId: { value: -1 },
                highlightColor: { value: new Color(0xFFC107) },
                unVisableBatchId: { value: -1 },
                ...UniformsUtils.clone(shader.uniforms),
            };
            //material["uniforms"] = shader.uniforms
            material["setHighlightedBatchId"]=(value:number)=>
            {
                shader.uniforms.highlightedBatchId.value=value as any; 
                shader.uniforms.unVisableBatchId.value=value as any; 
            }
            material["setHighlightColor"]=(value:Color)=>
            {
                shader.uniforms.highlightedBatchId.value=value as any; 
            }

            let vertexShader= shader.vertexShader.replace(
                /#include <uv_vertex>/,
                `
                #include <uv_vertex>
                batchid = _batchid;
                `
            );


            vertexShader= vertexShader.replace(
                /#include <fog_vertex>/,
                `
                #include <fog_vertex>
                if(abs( batchid - unVisableBatchId ) < 0.5 ) gl_Position = vec4( 0,0,0,0) ;
                `
            );

            // shader.extensions = {
            //     derivatives: true,
            // };
            // shader.lights = true;
            shader.vertexShader =
                `
                    attribute float _batchid;
                    varying float batchid;
                    uniform float unVisableBatchId;
                ` +
                vertexShader;

            //console.log( shader.vertexShader)


            shader.fragmentShader =
                `
                    varying float batchid;
                    uniform float highlightedBatchId;
                    uniform vec3 highlightColor;
                ` +
                shader.fragmentShader.replace(
                    /vec4 diffuseColor = vec4\( diffuse, opacity \);/,
                    `
                    vec4 diffuseColor =
                        abs( batchid - highlightedBatchId ) < 0.5 ?
                        vec4( highlightColor, 1 ) :
                        vec4( diffuse, opacity );
                    `
                );
        }
    }

    public static pickShader(material:Material) {

        material.onBeforeCompile = function (shader:Shader) {

            let vertexShader= shader.vertexShader.replace(
                /#include <fog_vertex>/,
                `
                #include <fog_vertex>
                float r = float((elementID >> 16) & 0xFF) / 255.0;
                float g = float((elementID >> 8) & 0xFF) / 255.0;
                float b = float((elementID >> 0) & 0xFF) / 255.0;
                vpickColor=vec3(r,g,b);
                `
            );

            shader.vertexShader =
                `
                attribute int elementID;
                varying  vec3 vpickColor;
                ` +
                vertexShader;


            shader.fragmentShader =
                `
                 varying vec3 vpickColor;
                ` +
                shader.fragmentShader.replace(
                    /#include <dithering_fragment>/,
                    `
                    #include <dithering_fragment>
                    gl_FragColor =vec4(vpickColor, gl_FragCoord.z);
                    `
                );

        }
    }

    public  static depethShader(material: Material) {
        material.onBeforeCompile = function (shader:Shader) {


            shader.uniforms = {
                far: { value: App.scene.camera.far },
                ...UniformsUtils.clone(shader.uniforms),
            };
            //material["uniforms"] = shader.uniforms
            material["setFar"]=(value:number)=>
            {
                shader.uniforms.far.value=value as any; 
            }

            let vertexShader= shader.vertexShader.replace(
                /#include <fog_vertex>/,
                `
                #include <fog_vertex>
                //gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
                depth = gl_Position.z / far;;
                `
            );

            shader.vertexShader =
                `
                precision highp float;
                uniform float far;
                varying float depth;
                ` +
                vertexShader;

           

            shader.fragmentShader =
                `
                precision highp float;
                varying float depth;
                ` +
                shader.fragmentShader.replace(
                    /#include <dithering_fragment>/,
                    `
                    #include <dithering_fragment>
                    float hex = abs(depth) * 16777215.0; // 0xffffff
                    float r = floor(hex / 65535.0);
                    float g = floor((hex - r * 65535.0) / 255.0);
                    float b = floor(hex - r * 65535.0 - g * 255.0);
                    float a = sign(depth) >= 0.0 ? 1.0 : 0.0; 
                    gl_FragColor = vec4(r / 255.0, g / 255.0, b / 255.0, a);
                    `
                );
        }
    }
}