import { ProjectViewComponent, Tree } from "@/components/ProjectViewComponent";
import { TilesRenderer } from "@/loaders/3d-tiles-renderer/three/TilesRenderer";
import { Box3, Object3D } from "three";
import { ElementData } from "./ElementData";
import { CategoryData, LevelData } from "./SpaceTreeData";



export class ModelData {
    


    //id1
    private static id: number = 1
    public readonly id: number;

    public rootObject: Object3D

    public allElementData: Map<number, ElementData> = new Map<number, ElementData>();

    public tile: TilesRenderer


    public boundingBox: Box3

    public name: string
    public url: string

    public levelDataMap: Map<string, LevelData>;


    constructor() {
        this.id = ModelData.id++
    }


    clear() {
        this.rootObject.parent.remove(this.rootObject)
        this.rootObject.clear()
        if( this.tile)
        {
            this.tile.dispose()
        }
        if(this.levelDataMap)
        {
            this.levelDataMap.clear()
        }
        this.allElementData.clear();
    }
       



    public getSpaceDatas(): Map<string, LevelData> {

        if (this.levelDataMap) return this.levelDataMap;
        this.levelDataMap = new Map();
        for (const id of this.allElementData.keys()) {
            let ele = this.allElementData.get(id)
            let categoryName = ele.useData["categoryName"]
            if (categoryName != null && typeof(categoryName)!='string') {
                categoryName=categoryName[0]
            }
            let levelName = ele.useData["level"]
            if (levelName != null && typeof(levelName)!='string') {
                levelName=levelName[0]
            }
            if (levelName != null) {
                {
                    let levelData: LevelData;
                    if (!this.levelDataMap.has(levelName)) {
                        levelData = new LevelData();
                        levelData.name = levelName;

                        let elevation= ele.useData["elevation"]
                        if (elevation != null && typeof(elevation)!='string') {
                            elevation=elevation[0]
                        }
                        levelData.elevation =elevation
                        levelData.categoryDataMap = new Map()
                        this.levelDataMap.set(levelName, levelData)
                    }
                    else {
                        levelData = this.levelDataMap.get(levelName);
                    }
                    if (categoryName != null) {
                        let categoryDataMap: CategoryData;
                        if (!levelData.categoryDataMap.has(categoryName)) {
                            categoryDataMap = new CategoryData();
                            categoryDataMap.name = categoryName;
                            categoryDataMap.elementDataMap = new Map()
                            levelData.categoryDataMap.set(categoryName, categoryDataMap)
                        }
                        else {
                            categoryDataMap = levelData.categoryDataMap.get(categoryName);
                        }
                        categoryDataMap.elementDataMap.set(id, ele)
                    }
                }
            }
        }
        return this.levelDataMap
    }


    public showSpaceTree(): Tree {
        this.getSpaceDatas();
        let levelDatas = Array.from(this.levelDataMap.values())
        if(levelDatas.length==0) return null;
        let showLevelDatas: Tree[] = [];
        let checkIds=[]
        //let start = 0;
        checkIds.push(0);
        levelDatas.sort(function (a, b) { return a.elevation - b.elevation })  
        let spaceData: Tree = {
            id: 0,
            label: this.name,
            children: showLevelDatas
        }
        for (const levelData of levelDatas) {
            let showCategoryDatas: Tree[] = [];
            checkIds.push(ElementData.id);
            let showLevelData: Tree = {
                id: ElementData.id++,
                label: levelData.name,
                children: showCategoryDatas
            }
            for (const categoryName of levelData.categoryDataMap.keys()) {

                let categoryData = levelData.categoryDataMap.get(categoryName);
                let showElementDatas: Tree[] = [];
                checkIds.push(ElementData.id);
                let showCategoryData: Tree = {
                    id: ElementData.id++,
                    label: categoryName,
                    children: showElementDatas
                }

                for (const elementData of categoryData.elementDataMap.values()) {
                    checkIds.push(elementData.id);
                    let showElementDataData: Tree = {
                        id: elementData.id,
                        label: elementData.name
                    }
                    showElementDatas.push(showElementDataData)
                }
                showCategoryDatas.push(showCategoryData)
            }
            showLevelDatas.push(showLevelData)
        }
        let data = [...ProjectViewComponent.ModelData.value]
        if (data == null || data.length == 0) {
            data = []
        }
        data.push(spaceData)
        ProjectViewComponent.ModelData.value = data
        ProjectViewComponent.SpaceTreeCheckIDs.value=checkIds
        ProjectViewComponent.SpaceTreeExpandedIDs.value=checkIds
    }
}