import { GlbLoaders } from "@/loaders/GlbLoaders";
import { Box3, Color, InstancedMesh, Matrix4, Vector3 } from "three";
import { MaterialData } from "./MaterialData";
import { MeshData } from "./MeshData";


export class InstanceData  implements MeshData
{

    private _instanceMesh: InstancedMesh;
    private _index: number;
    private _orignMatrix4:Matrix4;
    private _show:boolean=true;
    private _color:Color;


    constructor(mesh: InstancedMesh,index: number)
    {
        this._instanceMesh=mesh
        this._index=index;
        this._orignMatrix4=new Matrix4();
        this._instanceMesh.getMatrixAt(index, this._orignMatrix4)
    }

    public set show(value:boolean)
    {
        if(this._show==value) return;
        this._show=value;
        if(value)
        {
            this._instanceMesh.setMatrixAt(this._index,this._orignMatrix4)
        }
        else
        {
            this._instanceMesh.setMatrixAt(this._index, MaterialData.zeroMatrix4)
        }
        this._instanceMesh.instanceMatrix.needsUpdate = true;
    }

    public get show():boolean
    {
        return this._show;
    }

    public set color(value:Color)
    {

        if(value==null)
        {
            this._instanceMesh.setColorAt(this._index, MaterialData.normalColor);
        }
        else
        {
            this._instanceMesh.setColorAt(this._index, value);
        }
        this._instanceMesh.instanceColor.needsUpdate = true;
    }
    public get color():Color
    {
        return this._color;
    }



    public _boundingBox: Box3
    public set boundingBox(value: Box3) {
        this._boundingBox = value;
    }
    public get boundingBox(): Box3 {
        if (this._boundingBox == null) {
            this._boundingBox =this._instanceMesh.geometry.boundingBox.clone()
            let m = this._orignMatrix4.clone().multiply(this._instanceMesh.matrix);
            let getMatrix4 = (obj) => {
                if (obj.parent) {
                    m = m.premultiply(obj.parent.matrix);
                    getMatrix4(obj.parent)
                }
            }
            getMatrix4(this._instanceMesh);
            this._boundingBox.applyMatrix4(m)    
        }
        return this._boundingBox;
    }
}
