import { B3DMLoaderBase } from '../base/B3DMLoaderBase.js';
import { DefaultLoadingManager, Matrix4 ,Float32BufferAttribute,Color,Mesh,Int32BufferAttribute,BufferAttribute} from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { ElementData } from '@/data/ElementData';
import { BatchData } from '@/data/BatchData';
import { MaterialData } from '@/data/MaterialData';
export class B3DMLoader extends B3DMLoaderBase {

	constructor( manager = DefaultLoadingManager,tilesRenderer ) {

		super();
		this.manager = manager;
		this.adjustmentTransform = new Matrix4();
		this.tilesRenderer = tilesRenderer;

	}

	parse( buffer ) {

		const b3dm = super.parse( buffer );
		const gltfBuffer = b3dm.glbBytes.slice().buffer;
		return new Promise( ( resolve, reject ) => {

			const manager = this.manager;
			const fetchOptions = this.fetchOptions;
			const tilesRenderer = this.tilesRenderer;
			const loader = manager.getHandler( 'path.gltf' ) || new GLTFLoader( manager );

			if ( fetchOptions.credentials === 'include' && fetchOptions.mode === 'cors' ) {

				loader.setCrossOrigin( 'use-credentials' );

			}

			if ( 'credentials' in fetchOptions ) {

				loader.setWithCredentials( fetchOptions.credentials === 'include' );

			}

			if ( fetchOptions.headers ) {

				loader.setRequestHeader( fetchOptions.headers );

			}

			// GLTFLoader assumes the working path ends in a slash
			let workingPath = this.workingPath;
			if ( ! /[\\/]$/.test( workingPath ) && workingPath.length ) {

				workingPath += '/';

			}

			const adjustmentTransform = this.adjustmentTransform;

			loader.parse( gltfBuffer, workingPath, model => {

				const { batchTable, featureTable } = b3dm;
				const { scene } = model;

				const rtcCenter = featureTable.getData( 'RTC_CENTER' );
				if ( rtcCenter ) {

					scene.position.x += rtcCenter[ 0 ];
					scene.position.y += rtcCenter[ 1 ];
					scene.position.z += rtcCenter[ 2 ];

				}

				let classes = null;
				if (batchTable && batchTable.header && batchTable.header.extensions["3DTILES_batch_table_hierarchy"] != null) {
					classes = batchTable.header.extensions["3DTILES_batch_table_hierarchy"].classes;
				}
				//const pickColor = new Color();

				let mapElement=new Map()
				scene.traverse(function (child) {
					if (child instanceof Mesh) {

						child["orignMaterial"]=child.material;
						child["pickMaterial"]=MaterialData.pickMaterial
						child.material.vertexColors = true;

						let positionsCount = child.geometry.getAttribute("position").count;
						let colors =new BufferAttribute( new Float32Array( 3* positionsCount ), 3)
						//let pickColors = new BufferAttribute( new Float32Array( 3* positionsCount ), 3)
						let elementIDS =new BufferAttribute( new Int32Array( positionsCount ), 1)

						let indexs = child.geometry.index
						let batchids = child.geometry.getAttribute("_batchid");
						for (let i = 0; i < indexs.count; i++)
						{
							let index = indexs.getX(i)
							let id =Math.round(batchids.getX(index))
							let batchData;
							let element
							if (!mapElement.has(id)) {
								element=new ElementData();
								if(classes&&classes[id])
								{
									element.useData=classes[id].instances
								}
								element["mapbatchData"]=new Map()
								tilesRenderer.modelData.allElementData.set(element.id,element)
								mapElement.set(id, element)
								batchData = new BatchData(child, element.id)
								element.addMeshData(batchData)
								element["mapbatchData"].set(child.material, batchData)
							}
							else {

								element = mapElement.get(id);
								if (element["mapbatchData"].has(child.material)) {
									batchData = element["mapbatchData"].get(child.material)
								}
								else {
									batchData = new BatchData(child, element.id)
									element.addMeshData(batchData)
									element["mapbatchData"].set(child.material, batchData)
								}
							}
							elementIDS.setX(index,element.id)
							//pickColor.setHex( element.id);
							colors.setXYZ(index,1,1,1)


							// if (child.material.map == null&&child.material.opacity==0&&child.material.color) {     
							// 	colors.setXYZ(index,child.material.color.r,child.material.color.g,child.material.color.b)
							// }
							// else {
							// 	colors.setXYZ(index,1,1,1)
							// }
							// colors.setXYZ(index,child.material.color.r,child.material.color.g,child.material.color.b)
							//pickColors.setXYZ( i, pickColor.r, pickColor.g, pickColor.b );
							batchData.indexs.push(index)
						}
						//child.geometry.setAttribute('pickColor', pickColors);
						child.geometry.setAttribute('color', colors);
						child.geometry.setAttribute('elementID', elementIDS);
						child.geometry.deleteAttribute("_batchid")
					}
				})
				for (const element of mapElement.values()) {
					element.mapbatchData.clear();
					element.mapbatchData=null
				}

				model.scene.updateMatrix();
				model.scene.matrix.multiply( adjustmentTransform );
				model.scene.matrix.decompose( model.scene.position, model.scene.quaternion, model.scene.scale );

				// model.batchTable = batchTable;
				// model.featureTable = featureTable;

				// scene.batchTable = batchTable;
				// scene.featureTable = featureTable;

				resolve( model );

			}, reject );

		} );

	}

}
