import { Box3, Color } from "three";
import { MeshData } from "./MeshData";

/**
 * revitelement
 */
export class ElementData {

    //id1
    public static id: number=1


    public readonly id: number;
    public name: string;
    public useData: any;



    private _meshData:MeshData[]
    private _show: boolean;
    private _color:Color=null;

    //
    private _boundingBox:Box3
    constructor()
    {
        this.show=true;
        this._meshData=[]
        this.id=ElementData.id++;
    }

    public set show(value:boolean)
    {
        if(this._show==value) return;
        this._show=value;      
        if(this._meshData)
        {
            for (const mesh of this._meshData) {
                mesh.show=value
            }
        }
    }
    public get show():boolean
    {
        return this._show;
    }

    public set color(value:Color)
    {
        if(this._meshData)
        {
            for (const mesh of this._meshData) {
                mesh.color=value
            }
        }
    }
    public get color():Color
    {
        return this._color;
    }

    public addMeshData(mesh:MeshData)
    {
        this._meshData.push(mesh);
    }

    public set boundingBox(value:Box3)
    {
        this._boundingBox=value;
    }
    public get boundingBox():Box3
    {
        if(this._boundingBox==null)
        {
            if(this._meshData)
            {
                this._boundingBox=new Box3();
                for (const mesh of this._meshData) {
                    this._boundingBox.union(mesh.boundingBox)
                }
            }
        }

        return this._boundingBox;
    }
}