    /**
     * 
     */
    public render() {
        
        if( this.firstPerson&&!this._gravityIng&& this._gravity)
        {
            this.gravity(true)
        }


        for (let i = 0; i < this.planeDatas?.length; i++) {
            let planeData = this.planeDatas[i];
            const plane = planeData.plane;
            const po = planeData.planeObject;
            plane.coplanarPoint(po.position);
            po.lookAt(
                po.position.x - plane.normal.x,
                po.position.y - plane.normal.y,
                po.position.z - plane.normal.z,
            );
        }



        for (const iterator of this.mapModels.values()) {
            if(iterator.tile)
            {
                iterator.tile.update();
            }
        }
        this.renderer.clear()
        //this.renderer.autoClear = false;
        this.renderer.setViewport(0, 0, this.goldenContainer.clientWidth,  this.goldenContainer.clientHeight);

        this.renderer.render(this.scene, this.camera);
       //console.log(this.controls["_target"])
        //
        this.viewCube.cameraOrtho.position.copy(this.controls.getPosition());
        this.viewCube.cameraOrtho.position.sub( this.controls.getTarget());
        this.viewCube.cameraOrtho.lookAt(this.scene.position);
        let dir= this.viewCube.cameraOrtho.position.normalize();
        this.viewCube.cameraOrtho.position.x=dir.x*50;
        this.viewCube.cameraOrtho.position.y=dir.y*50;
        this.viewCube.cameraOrtho.position.z=dir.z*50;
        this.renderer.setViewport(this.goldenContainer.clientWidth-170,this.goldenContainer.clientHeight-170, 120, 120)
        this.renderer.render(this.viewCube.sceneOrtho, this.viewCube.cameraOrtho);
        //this.controls.update();
    }


    public clearModel()
