    public setFirstPerson(value:boolean=true)
    {
        this.firstPerson=value
        if (value) {
            if(this.keyDownEvents==null)
            {
                let holdIntervalDelay=16.666;
                this.keyDownEvents = new Map()
                this.keyDownEvents.set("KeyW", new KeyboardKeyHold("KeyW", holdIntervalDelay))
                this.keyDownEvents.set("KeyA", new KeyboardKeyHold("KeyA", holdIntervalDelay))
                this.keyDownEvents.set("KeyS", new KeyboardKeyHold("KeyS", holdIntervalDelay))
                this.keyDownEvents.set("KeyD", new KeyboardKeyHold("KeyD", holdIntervalDelay))
                this.keyDownEvents.set("KeyQ", new KeyboardKeyHold("KeyQ", holdIntervalDelay))
                this.keyDownEvents.set("KeyE", new KeyboardKeyHold("KeyE", holdIntervalDelay))
            }

            if(this.point==null)
            {
                this.point=document.createElement("div");
                this.point.style.position="absolute"
                this.point.style.background="#ff0000"
                this.point.style.top="50%"
                this.point.style.left="50%"
                this.point.style.width="10px"
                this.point.style.height="10px"
                this.point.style.border="2px"
                this.point.style.borderRadius="5px"
            }
            this.goldenContainer.appendChild(this.point)

            this.controls.minDistance = this.controls.maxDistance = 0.000001;
            this.controls.azimuthRotateSpeed = - 0.3; // negative value to invert rotation direction
            this.controls.polarRotateSpeed = - 0.3; // negative value to invert rotation direction
            this.controls.truckSpeed = 10;
            this.controls.mouseButtons.wheel = CameraControls.ACTION.NONE;
            this.controls.mouseButtons.right = CameraControls.ACTION.ROTATE;
            this.controls.mouseButtons.left = CameraControls.ACTION.ROTATE;
            this.controls.touches.two = CameraControls.ACTION.TOUCH_ZOOM_TRUCK;
            this.curTarget= this.controls.curTarget.clone();
            //this.control.setLookAt
            //this.controls.setTarget
            // document.addEventListener("keydown", this.keyDownEvent);
            //this.controls.setLookAt(this.camera.position.x,this.camera.position.y,this.camera.position.z,   this.curTarget.x, this.curTarget.y, this.curTarget.z)
            this.controls.setOrbitPoint(this.camera.position.x,this.camera.position.y,this.camera.position.z)
            //this.controls.forward(0.00001)
            this.controls.update()
            for (const evt of  this.keyDownEvents.values()) {
                evt.addEventListener( 'holding', this.keyDownEvent );
            }
            //this.camera.setFocalLength (18);
        }
        else
        {
           
            //this.camera.fov=45;
            this.controls.minDistance =0
            this.controls.maxDistance =Infinity
            this.controls.azimuthRotateSpeed = 1;
            this.controls.polarRotateSpeed = 1;
            this.controls.truckSpeed = 2;
            this.controls.mouseButtons.wheel = CameraControls.ACTION.DOLLY;
            this.controls.mouseButtons.right = CameraControls.ACTION.ROTATE;
            this.controls.mouseButtons.left = CameraControls.ACTION.OFFSET;
            this.controls.touches.two = CameraControls.ACTION.TOUCH_DOLLY_TRUCK;
            //document.removeEventListener("keydown", this.keyDownEvent);
            this.controls.setOrbitPoint(this.curTarget.x,this.curTarget.y,this.curTarget.z)
             this.controls.update()       
            for (const evt of  this.keyDownEvents.values()) {
                evt.removeEventListener( 'holding', this.keyDownEvent );
            }
            if(this.point)
            {
                this.goldenContainer.removeChild(this.point)
            }
        }
     
        this.render();

    }


    private initStats()
    {
        this.stats=Stats();
        this.stats.setMode(0);
        this.stats.domElement.style.position = 'absolute';
        this.stats.domElement.style.left = '0px';
        this.stats.domElement.style.top = '0px';
        this.goldenContainer.appendChild(this.stats.domElement);
    }


   
    private animate = () => {
        requestAnimationFrame(this.animate)


        if(this.autoRender)
        {
            const updated = this.controls.update();
            if (updated) {
                this.render();
            }
        }
        else
        {
            if(this.renderEnabled)
            {
                this.render();
                this.renderEnabled=false;
            }
        }
        this.stats.update();

       

        // this.controls.azimuthAngle += 2  * THREE.MathUtils.DEG2RAD;
        // this.controls.update();
        // this.renderEnabled=true

        //this.vignette.style({aspect: this.camera.aspect});
    }


   




   

    private moveTo(x:number,y:number,z:number)
