    public setCameraByBox(box:THREE.Box3) {
        //
        let center = new THREE.Vector3((box.max.x + box.min.x) / 2, (box.max.y + box.min.y) / 2, (box.max.z + box.min.z) / 2);
        this.controls.fitToBox(box, false,{
            cover :false,
            paddingLeft : 0,
            paddingRight : 0,
            paddingBottom : 0,
            paddingTop :0
        })
        this.moveTo(center.x, center.y, center.z)   
        this.enableRender()
        //this.homeBoxData=this.controls.toJSON()
        this.homeTarget= this.controls.getTarget()
        this.homePosition= this.controls.getPosition()
    }


